const side = 3
export default {
  props: {
    metaInfo: {
      type: Object,
      default() {
        return {
          currentPage: 1,
          totalElements: 1,
          totalPages: 1,
        }
      },
    },
  },
  computed: {
    // сколько всего страниц
    nextLinks() {
      const items = this.metaInfo.totalPages - this.metaInfo.currentPage
      return items > side ? side : items < 0 ? 0 : items
    },
    prevLinks() {
      const items = this.metaInfo.currentPage - 1
      return items > side ? side : items <= 0 ? 0 : items
    },
    route() {
      return this.$route.name
    },
  },
}
